<template>
  <v-container
    v-if="logado == 'true'"
    fluid
    tag="section"
  >
    <v-overlay :value="showUpdate">
      <v-alert
        icon="mdi-check"
        prominent
        shaped
        type="success"
        transition="scale-transition"
        class="alert-banner"
      >
        Perfil atualizado com sucesso!
      </v-alert>
    </v-overlay>
    <v-overlay :value="showError">
      <v-alert
        icon="mdi-alert"
        prominent
        shaped
        type="error"
        transition="scale-transition"
        class="position-fixed"
      >
        Erro ao atualizar perfil.
      </v-alert>
    </v-overlay>

    <v-row justify="center">
      <v-col
        cols="12"
        md="8"
      >
        <v-card class="mt-7 mb-10 mb-md-0">
          <v-row>
            <v-col cols="12">
              <h3 class="font-weight-medium ms-5 mb-10">
                Editar Perfil — <small class="text-body-1">Atualize as informações da empresa</small>
              </h3>
            </v-col>
          </v-row>

          <v-form>
            <v-container class="py-0">
              <v-row>
                <v-col
                  cols="6"
                  md="3"
                >
                  <v-text-field
                    v-model="items.RAZAOSOCIAL"
                    disabled
                    label="Empresa"
                    outlined
                  />
                </v-col>

                <v-col
                  cols="6"
                  md="3"
                >
                  <v-text-field
                    v-model="items.CNPJ"
                    disabled
                    color="blue"
                    :label="items.TIPO === 'PJ' ? 'CNPJ' : 'CPF'"
                    outlined
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="6"
                >
                  <v-text-field
                    v-model="items.CONTATO1"
                    color="blue"
                    label="Contato"
                    outlined
                  />
                </v-col>

                <v-col
                  cols="8"
                  md="9"
                >
                  <v-text-field
                    v-model="items.LOGRADOURO"
                    color="blue"
                    label="Logradouro"
                    outlined
                    disabled
                  />
                </v-col>

                <v-col
                  cols="4"
                  md="3"
                >
                  <v-text-field
                    v-model="items.NUM"
                    color="blue"
                    label="Número"
                    outlined
                  />
                </v-col>

                <v-col
                  cols="6"
                  md="4"
                >
                  <v-text-field
                    v-model="items.ESTADO"
                    color="blue"
                    label="Estado"
                    outlined
                    disabled
                  />
                </v-col>

                <v-col
                  cols="6"
                  md="4"
                >
                  <v-text-field
                    v-model="items.CIDADE"
                    color="blue"
                    label="Cidade"
                    outlined
                    disabled
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="4"
                >
                  <v-text-field
                    v-model="items.CEP"
                    v-mask="'########'"
                    :rules="rulesCEP"
                    required
                    color="blue"
                    name="cep"
                    label="CEP"
                    outlined
                    @keyup="preencherCep"
                  />
                </v-col>

                <v-col
                  cols="12"
                  class="text-right"
                >
                  <v-btn
                    color="primary"
                    min-width="150"
                    @click="editProfile"
                  >
                    Atualizar Perfil
                  </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios'
  import Vue from 'vue'

  export default {
    name: 'UserProfileView',

    data: () => ({
      erroCep: false,
      showUpdate: false,
      showProfileUpdate: false,
      showError: false,
      logado: window.sessionStorage.logado,
      err: '',
      items: {
        LOGRADOURO: '',
        ESTADO: '',
        CIDADE: '',
        ID: window.sessionStorage.empid,
        CNPJ: window.sessionStorage.empcnpj,
        CEP: window.sessionStorage.empcep,
        NUM: window.sessionStorage.empnum,
        CONTATO1: window.sessionStorage.empcontato,
        TIPO: window.sessionStorage.emptipo,
        RAZAOSOCIAL: window.sessionStorage.razaosocial,
      },
    }),

    computed: {
      rulesCEP () {
        const rules = []

        const rule = (this.erroCep === false && this.items.CEP.length === 8) || 'CEP inválido'
        rules.push(rule)

        return rules
      },
    },

    watch: {
      items: {
        handler () {
          window.sessionStorage.empcontato = this.items.CONTATO1
          window.sessionStorage.empcep = this.items.CEP
          window.sessionStorage.empnum = this.items.NUM
        },
        deep: true,
      },
      err () {
        alert(this.err)
      },
    },

    created () {
      Vue.config.warnHandler = (err => this.err = err)
    },

    beforeMount () {
      this.preencherCep()
    },

    methods: {
      getCep (cep) {
        return axios.get(`https://viacep.com.br/ws/${cep}/json/`)
      },
      preencherCep () {
        const cep = this.items.CEP.replace(/\D/g, '')
        if (cep.length === 8) {
          this.getCep(cep)
            .then(r => {
              this.items.LOGRADOURO = r.data.logradouro
              this.items.ESTADO = r.data.uf
              this.items.CIDADE = r.data.localidade
              'erro' in r.data ? this.erroCep = true : this.erroCep = false
            })
        }
      },

      editProfile () {
        axios.put(`https://sheetdb.io/api/v1/8nmjdq4kt9z0x/IDEMPRESA/${this.items.ID}`, {
          data: {
            CONTATO01: `${this.items.CONTATO1}`,
            NUM: `${this.items.NUM}`,
            CEP: `${this.items.CEP}`,
          },
        })
          .then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showUpdate = !this.showUpdate
              setTimeout(() => {
                this.showUpdate = !this.showUpdate
              }, 2500)
            }
          })
          .catch(error => {
            if (error.response) {
              this.showError = !this.showError
              setTimeout(() => {
                this.showError = !this.showError
              }, 2500)
            }
          })
      },
    },
  }
</script>
